.stats-cards {
    flex: 1; 
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    gap: 20px;
    padding: 30px 0;
  
    @media (max-width: 768px) {
      flex-basis: 100%;
      flex-direction: row;
      align-items: center;
      justify-content: center;
    }
}